import Request from '../../util/luch-request/request.js'
import common from '../../common/globalVariable.js'
export default{
	data(){
		return {
			userId:'',
			fromNick:'',
			icon:'',
			sex:'',
			second:''
		}
	},
	onLoad: function (option) {
		var THAT = this;
		console.log(option);
		this.sex = common.sex;
		this.userId=option.userId;
		this.second=this.timeStamp(option.second);
		this.getUser();
	},
	methods:{
		closePage(){
			uni.navigateBack({
			    delta: 1
			});
		},
		timeStamp( second_time ){
		var time = parseInt(second_time) + "秒";  
		if( parseInt(second_time )> 60){  
		    var second = parseInt(second_time) % 60;  
		    var min = parseInt(second_time / 60);  
		    time = min + "分" + second + "秒";  
		    if( min > 60 ){  
		        min = parseInt(second_time / 60) % 60;  
		        var hour = parseInt( parseInt(second_time / 60) /60 );  
		        // time = hour + "小时" + min + "分" + second + "秒";  
				time = hour + "小时" + min + "分";
		        if( hour > 24 ){  
		            hour = parseInt( parseInt(second_time / 60) /60 ) % 24;  
		            var day = parseInt( parseInt( parseInt(second_time / 60) /60 ) / 24 );  
		            // time = day + "天" + hour + "小时" + min + "分" + second + "秒"; 
					time = day + "天" + hour + "小时";
		        }  
		    }  
		}  
		return time;          
		},
		getUser(){
			var THAT = this;
			const http = new Request();
			let params={
				params:{
					id:THAT.userId,
					cerStatus:''
				}
			}
			http.get('/user', params).then(res => {
				console.log(res);
				THAT.fromNick = res.data.result.user.userName;
				THAT.icon = res.data.result.user.icon;
				uni.setNavigationBarTitle({
					 title: THAT.fromNick
				})
			})
		}
	}
}
